<template>
  <!--출국정보 목록-->
  <div class="swiperWrap swiperDivi swipeType02" data-slide-per-view="auto">
    <div class="swiper-container">
      <button type="button" class="swiper-button-prev">이전</button>
      <ul class="swiper-wrapper">
        <li
          v-for="(item, index) in parsedData"
          v-bind:key="index"
          class="swiper-slide"
        >
          <div>
            <div class="swTxts">
              <span>
                <span>{{ $t("departure_infomation.출발지") }}</span>
                <span>{{ item.depaPlacNm }}</span>
              </span>
              <span>
                <span>{{ $t("departure_infomation.출발시간") }}</span>
                <span>{{ dpatExptDttm(item) }}</span>
              </span>
              <span>
                <span>{{ $t("departure_infomation.편명") }}</span>
                <span>{{ openNm(item) }}</span>
                <!-- <span>{{ item.openNm }}/{{ item | drflYn }}</span> -->
              </span>
            </div>
            <div class="swBtns">
              <template v-if="item.ordCnt == 0 || item.exitPossYn == 'N'">
                <button
                  v-if="item.exitPossYn == 'N'"
                  type="button"
                  class="btnSSG btnM"
                  disabled
                >
                  {{ $t("departure_infomation.변경불가") }}
                </button>
                <button
                  @click="click('변경', item)"
                  v-if="item.exitPossYn == 'Y'"
                  type="button"
                  class="btnSSG btnM btnLB"
                  :class="isOnConsulting ? 'cb-disabled-button' : ''"
                >
                  {{ $t("departure_infomation.변경") }}
                </button>
              </template>
              <button
                @click="click('주문내역변경', item)"
                v-if="item.ordCnt > 0 && item.exitPossYn == 'Y'"
                type="button"
                class="btnSSG btnM btnLB"
                :class="isOnConsulting ? 'cb-disabled-button' : ''"
              >
                {{ $t("departure_infomation.주문내역변경") }}
              </button>
            </div>
          </div>
        </li>
        <!--캐러셀 항목 5개 이상일시에만 노출-->
        <li v-if="data.list.length > 5" class="swiper-slide btnMors">
          <button @click="click('더보기')" type="button" class="btMore">
            {{ $t("common.더보기") }}
          </button>
        </li>
      </ul>
      <button type="button" class="swiper-button-next">다음</button>
    </div>
  </div>
  <!--//출국정보 목록-->
</template>

<script>
import consultingStatus from '@/mixins/consultingStatus';
// import $ from 'jquery';

export default {
  name: 'DepartureInformationCard',
  mixins: [consultingStatus],

  props: {
    data: {},
  },
  mounted() {
    this.$uiHandler.swiperNextPrevShow();
  },
  computed: {
    parsedData() {
      if (this.data.list.length > 5) {
        return this.data.list.slice(0, 5);
      }

      return this.data.list;
    },
  },
  filters: {
  },
  methods: {
    openNm(item) {
      const drflYn = item.drflYn === 'Y' ? this.$t('departure_infomation.직항') : this.$t('departure_infomation.경유');
      return item.openNm === '' ? this.$t('departure_infomation.미등록') : `${item.openNm}/${drflYn}`;
    },
    dpatExptDttm(item) {
      return item.dpatExptDttm === '' ? this.$t('departure_infomation.미등록') : item.dpatExptDttm;
    },
    /*
    openWindowWithPost(url, data, target) {
      $(`<form action="${url}" method="POST" target="${target}" name="orderChange"></form>`)
      .append($(`<input name="selOrdList" value="${ [data.ord_no] }">`))
      .appendTo($('body'))
      .submit();
    },
    openWindowWithPost2(url, regseq, target) {
      $(`<form action="${url}" method="POST" target="${target}" name="departureChange"></form>`)
      .append($(`<input name="reg_seq" value="${ regseq }">`))
      .appendTo($('body'))
      .submit();
    },
    */
    async click(id, item) {
      switch (id) {
        case '변경': { // 출국정보수정
          this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_202);

          const { userAgent } = window.navigator;
          if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(userAgent)) {
            const { urlEdit } = item;
            const urlArray = urlEdit.split('?');
            const url = `${urlArray[0].slice(0, -8)}Detail`;
            /*
            if (window.fromApp) {
              this.openWindowWithPost2(url, urlArray[1].slice(8), '');
            } else {
              this.openWindowWithPost2(url, urlArray[1].slice(8), 'login_page');
            }
            */
            window.cbMoveParent(`${url}?reg_seq=${urlArray[1].slice(8)}`);
          } else {
            window.cbMoveParent(item.urlEdit);
          }
          break;
        }
        case '주문내역변경':
          this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_203);
          if (item.ordCnt > 1) {
            window.cbMoveParent(this.data.urls.주문내역목록);
          /* } else if (window.fromApp) {
            this.openWindowWithPost(this.data.urls.주문내역, { ord_no: item.ordNo }, '');
          } else {
            this.openWindowWithPost(this.data.urls.주문내역, { ord_no: item.ordNo }, 'login_page');
          } */
          } else {
            window.cbMoveParent(`${this.data.urls.주문내역}?selOrdList=${item.ordNo}`);
          }
          break;
        case '더보기':
          this.$analyticsHandler.apply(this.$dictionary.tagType.CB_BTN_KR_204);
          window.cbMoveParent(this.data.urls.출국정보목록);
          break;
        default:
          break;
      }
    },
  },
};
</script>